.header {
  padding: 0 24px;
  color: white;
  height: 700px;
  background-image: url(../images/header.jpg);
  background-size: cover;
  background-position: center;
  position: relative;
  font-weight: bold;
  font-family: "Roboto", sans-serif; }
  .header__title {
    font-weight: bold;
    font-size: 60px;
    margin: 0; }
    .header__title--wrapper {
      padding-top: 16px;
      position: absolute;
      left: 0;
      top: 324px; }
      .header__title--wrapper ::before {
        content: "";
        background-color: white;
        position: absolute;
        left: 0;
        top: 0;
        width: 35px;
        height: 2px; }
  .header__nav {
    text-transform: uppercase;
    position: absolute;
    bottom: 56px;
    left: 0;
    right: 0; }
  .header__top {
    text-transform: uppercase;
    position: absolute;
    top: 24px;
    left: 0;
    right: 0;
    justify-content: space-between;
    color: white;
    display: flex;
    align-items: center; }
  .header__link {
    color: white;
    text-decoration: none;
    border-bottom: 1px solid white;
    transition: color 0.3s; }
    .header__link:hover {
      color: #9fadc2; }

